.app {
  box-sizing: border-box;
  position: relative;
  max-width: 600px;
  margin: 0 auto;
}

.header {
  box-sizing: border-box;
  background-color: #FFBC53;
}

.card {
  display: flex;
  justify-content: space-between;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: normal;
  color: white;
  padding: 16px;

  &__weather {
    font-size: 48px;
    line-height: 56px;
  }

  &__temp {
    font-weight: bold;
  }

  &__condition {
    font-size: 24px;
    line-height: 28px;
    font-weight: bold;
  }
}

.term {
  font-size: 18px;
  line-height: 21px;

  &__label-m {
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
  }
}

.forecast-day {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: bold;
  width: 100%;
  color: white;
  background-color: #AEBFD1;
  margin-bottom: 1px;
  box-shadow:  0px 1px 2px rgba(0, 0, 0, 0.25);
  border: 0;
  padding: 0;
  text-align: left;
  cursor: pointer;

  &__header {
    box-sizing: border-box;
    display: flex;
    font-size: 24px;
    line-height: 28px;
    box-shadow:  0px 1px 2px rgba(0, 0, 0, 0.25);
    height: 64px;
  }

  &__column {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    width: 50%;
  }

  &__dayName {
    padding-left: 16px;
  }

  &__arrow {
    width: 12px;
    height: 12px;
    margin-right: 12px;
  }

  &__icon {
    width: 33.3%;
    height: 100%;
    display: flex;
    justify-content: center;

    img { 
      width: 50%;
      height: auto;
    }
  }

  &__max {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 33.3%;
    height: 100%;
    justify-content: center;
    background: rgba(255,255,255,.15);
  }

  &__min {
    width: 33.3%;
  }
}

.arrow-enter {
  transform: rotate(0deg);
}

.arrow-enter-active {
  transform: rotate(180deg);
  transition: transform linear 300ms;
}

.arrow-enter-done {
  transform: rotate(180deg);
}

.arrow-exit {
  transform: rotate(180deg);
}

.arrow-exit-active {
  transform: rotate(360deg);
  transition: transform linear 300ms;
}

.forecast__row-full {
  box-sizing: border-box;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}

.row-full-enter {
  max-height: 0;
  opacity: 0;
}

.row-full-enter-active {
  max-height: 180px;
  overflow: hidden;
  opacity: 1;
  transition: 300ms linear;
}

.row-full-exit {
  max-height: 180px;
  opacity: 1;
}

.row-full-exit-active {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: 300ms linear;
}

.row__item {
  display: flex;
  align-items: center;
}

